import {
  React,
  connect,
  Link,
  reduxForm,
  Panel,
  PanelHeader,
  getDataNoTimeOut,
  formatDate,
  moment,
  ToastNotification,
  localStorageencryp,
  PanelBody,
} from "../../../../components/helpers/library";
import HeadLaporanStockJual from "./HeadLaporanStockJual";

class LaporanStockJual extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      isLoading: false,
      LaporanStockJual: [],
      export: false,
    };
    this.handleSubmit = this.handleSubmit.bind(this);
  }

  handleSubmit(data) {
    let data_post = {
      tgl_awal: formatDate(
        moment(new Date(data.tgl_awal)).format("YYYY-MM-DD")
      ),
      tgl_akhir: formatDate(
        moment(new Date(data.tgl_akhir)).format("YYYY-MM-DD")
      ),
      kode_dept : data.kode_dept,
      kode_baki : data.kode_baki,
    };
    this.setState({
      isLoading: true,
    });
    getDataNoTimeOut(
      "penjualan/report-stock-terjual?tgl_from=" +
        data_post.tgl_awal +
        "&tgl_to=" +
        data_post.tgl_akhir +
        "&kode_dept=" +
        data_post.kode_dept+
        "&kode_baki=" +
        data_post.kode_baki
    )
      .then((res) => {
        // console.log(res);
        if (res.data.length === 0) {
          ToastNotification("info", "Laporan Stock Terjual Kosong");
          this.setState({
            export: false,
            isLoading: false,
          });
          return false;
        }
        ToastNotification("success", "Laporan Stock Jual Tersedia");
        localStorageencryp("tgl_laporan", JSON.stringify(data_post));
        this.setState({
          LaporanStockJual: res.data,
          isLoading: false,
          export: true,
        });
      })
      .catch((err) => {
        ToastNotification("info", "Data Yang Di Cari Tidak Ada !!!");
        this.setState({
          export: false,
          isLoading: false,
        });
      });
  }

  render() {
    return (
      <div>
        <ol className="breadcrumb float-xl-right">
          <li className="breadcrumb-item">
            <Link to="#">Laporan</Link>
          </li>
          <li className="breadcrumb-item active">Laporan Stock Jual</li>
        </ol>
        <h1 className="page-header">Laporan Stock Jual </h1>
        <Panel>
          <PanelHeader>Laporan Stock Jual</PanelHeader>
          <br />
          {/* Blank Page */}
          <PanelBody>
            <HeadLaporanStockJual
              export={this.state.export}
              isLoading={this.state.isLoading}
              LaporanStockJual={this.state.LaporanStockJual}
              onSubmit={(data) => this.handleSubmit(data)}
            />
          </PanelBody>
          <br />
          {/* End Tambah Blank Page  */}
        </Panel>
      </div>
    );
  }
}

LaporanStockJual = reduxForm({
  form: "LaporanStockJual",
  enableReinitialize: true,
})(LaporanStockJual);
export default connect(null)(LaporanStockJual);
