import React, { Component } from "react";
import { connect } from "react-redux";
import { Field, reduxForm } from "redux-form";
import { InputDate, ReanderSelect } from "../../../../components/helpers/field";
import {
  formatGram,
  getTglSystem,
} from "../../../../components/helpers/function";
import Tabel from "../../../../components/helpers/tabel";
import ValidasiTanggalLaporan from "../../../../Validasi/ValidasiTanggalLaporan";
import Loading from "react-fullscreen-loading";
import LaporanStockJualPdf from "./pdf/LaporanStockJualPdf";
import LaporanStockJualExel from "./exel/LaporanStockJualExel";
import {
  getDataBaki,
  getDataJenis,
} from "../../../../actions/datamaster_action";
class HeadLaporanServiceAmbil extends Component {
  constructor(props) {
    super(props);
    this.state = {
      date: new Date(),
    };
    this.setStartDate = this.setStartDate.bind(this);
    this.setLastDate = this.setLastDate.bind(this);
  }

  exportPdf() {
    LaporanStockJualPdf(this.props.LaporanStockJual);
  }
  setStartDate(date) {
    this.setState({
      tgl_awal: new Date(date),
    });
  }
  setLastDate(date) {
    this.setState({
      tgl_akhir: new Date(date),
    });
  }
  componentDidMount() {
    this.props.dispatch(getDataJenis());
    this.props.dispatch(getDataBaki());
    setTimeout(() => {
      this.props.change("kode_dept","ALL")
      this.props.change("kode_baki","ALL")
  }, 100);
  }

  render() {
    let databaki = [
      {
        value: "ALL",
        name: "SEMUA",
      },
    ];
    this.props.databaki.forEach((list) => {
      let data = {
        value: list.kode_baki,
        name: list.kode_baki + " - " + list.nama_baki,
      };
      databaki.push(data);
    });
    let dataJenis = [
      {
        value: "ALL",
        name: "SEMUA",
      },
    ];
    this.props.dataJenis.forEach((list) => {
      let data = {
        value: list.kode_dept,
        name: list.kode_dept + " - " + list.nama_dept,
      };
      dataJenis.push(data);
    });
    const columns = [
      {
        dataField: "kode_barcode",
        text: "Kode Barcode",
        footerAttrs: {
          colSpan: "8",
        },
        footer: (coulmdata) => {
          return <div>Total Transaksi : {coulmdata.length} </div>;
        },
      },
      {
        dataField: "kode_dept",
        text: "Kode Jenis",
      },
      {
        dataField: "nama_barang",
        text: "Nama Barang",
      },
      {
        dataField: "berat",
        text: "Berat",
        headerClasses: "text-right",

        formatter: (cell) => {
          return <div className="text-right">{formatGram(cell, 3)}</div>;
        },
      },
      {
        dataField: "harga_gram",
        text: "Harga / Gram",
        headerClasses: "text-right",

        formatter: (cell) => {
          return (
            <div className="text-right">{cell.toLocaleString("kr-KO")}</div>
          );
        },
      },
      {
        dataField: "harga_jual",
        text: "Harga Jual",
        headerClasses: "text-right",

        formatter: (cell) => {
          return (
            <div className="text-right">{cell.toLocaleString("kr-KO")}</div>
          );
        },
      },
      {
        dataField: "harga_atribut",
        text: "Harga Atribut",
        headerClasses: "text-right",

        formatter: (cell) => {
          return (
            <div className="text-right">{cell.toLocaleString("kr-KO")}</div>
          );
        },
      },
      {
        dataField: "ongkos",
        text: "Ongkos",
        headerClasses: "text-right",

        formatter: (cell) => {
          return (
            <div className="text-right">{cell.toLocaleString("kr-KO")}</div>
          );
        },
      },
      {
        dataField: "harga_total",
        text: "Harga Total",
        headerClasses: "text-right",
        footer: (cell, row, num, index) => {
          return (
            <div className="text-right">
              {cell.reduce((a, b) => a + b, 0).toLocaleString("kr-KO")}
            </div>
          );
        },
        formatter: (cell) => {
          return (
            <div className="text-right">{cell.toLocaleString("kr-KO")}</div>
          );
        },
      },
    ];
    return (
      <form onSubmit={this.props.handleSubmit} autoComplete="off">
        <div className="row">
          <div className="col-lg-2">
            <Field
              name="tgl_awal"
              component={InputDate}
              label="Tanggal Dari"
              type="text"
              selected={this.state.tgl_awal}
              onChange={(date) => this.setStartDate(date)}
              placeholder="Masukan Tanggal Dari"
            />
          </div>

          <div className="col-lg-2">
            <Field
              name="tgl_akhir"
              component={InputDate}
              type="text"
              selected={this.state.tgl_akhir}
              onChange={(date) => this.setLastDate(date)}
              label="Tanggal Akhir"
              placeholder="Masukan Tanggal Akhir"
            />
          </div>
          <div className="col-lg-2">
            <Field
              label="Jenis"
              name="kode_dept"
              options={dataJenis}
              placeholder={"Silahkan Pilih Kode Jenis"}
              component={ReanderSelect}
            />
          </div>
          <div className="col-lg-2">
            <Field
              label="Kode Baki"
              name="kode_baki"
              options={databaki}
              placeholder={"Silahkan Pilih Kode Baki"}
              component={ReanderSelect}
            />
          </div>
          <div className="col-lg-4">
            <div className="text-right">
              <label>&nbsp;</label>
              <button
                type="submit"
                value="PDF"
                name="type_btn"
                className="btn btn-primary btn-block"
              >
                {this.props.isLoading ? (
                  <>
                    <i className="fas fa-spinner fa-spin"></i> &nbsp; Sedang
                    Mencari
                  </>
                ) : (
                  "Cari Laporan"
                )}
              </button>
            </div>
          </div>
          <div className="col-lg-12">
            <Tabel
              keyField="kode_barcode"
              data={this.props.export ? this.props.LaporanStockJual : []}
              columns={columns}
              empty={true}
              textEmpty="Data Laporan Stock Jual Kosong"
            />
          </div>
          <div className={this.props.export ? "col-lg-12" : "col-lg-12 d-none"}>
            {/* <label htmlFor="">&nbsp;</label> */}
            <div className="row">
              <div className="col-lg-6">
                <button
                  type="button"
                  onClick={() => this.exportPdf()}
                  className="btn btn-warning btn-block"
                >
                  {" "}
                  Export PDF{" "}
                </button>
              </div>
              <div className="col-lg-6">
                <LaporanStockJualExel data={this.props.LaporanStockJual} />
              </div>
            </div>
          </div>
        </div>
        {this.props.isLoading ? (
          <Loading
            loading
            background="rgba(0, 0, 0, 0.35)"
            loaderColor="rgba(94, 147, 117, 1)"
          />
        ) : null}
      </form>
    );
  }
}

HeadLaporanServiceAmbil = reduxForm({
  form: "HeadLaporanServiceAmbil",
  enableReinitialize: true,
  validate: ValidasiTanggalLaporan,
})(HeadLaporanServiceAmbil);
// export default connect()(HeadLaporanServiceAmbil);
export default connect((state) => {
  return {
    databaki: state.datamaster.getDataBaki,
    dataJenis: state.datamaster.getDataJenis,
    initialValues: {
      tgl_awal: getTglSystem(),
      tgl_akhir: getTglSystem(),
    },
  };
})(HeadLaporanServiceAmbil);
